<script>
  import * as L from 'https://cdn.skypack.dev/leaflet';


  let map;
  export let lat = 38.897957;
  export let lon = -77.036560;
  export let button = true;

  

  function useCurrentLocation() {    
      navigator.geolocation.getCurrentPosition((position) => {
          console.log(lat);
          lat    = position.coords.latitude;
          lon    = position.coords.longitude;
          map.setView([lat,lon], 13);
      })
  }

  function createMap(container) {
    const m = L.map(container).setView([lat, lon], 13);
    const myIcon = L.icon({
      iconUrl: 'data:image/png;base64,',
      iconAnchor: [12.5,41]
      })
    const marker = L.marker([lat, lon], {icon: myIcon}).addTo(m);
    L.tileLayer(
      'https://{s}.basemaps.cartocdn.com/rastertiles/voyager/{z}/{x}/{y}{r}.png',
      {
        attribution: `&copy;<a href="https://www.openstreetmap.org/copyright" target="_blank">OpenStreetMap</a>,
          &copy;<a href="https://carto.com/attributions" target="_blank">CARTO</a>`,
        subdomains: 'abcd',
        maxZoom: 20,
      }
    ).addTo(m);
    return m;
  }

  function mapAction(container) {
    map = createMap(container);
    return {
      destroy: () => {
        map.remove();
      },
    };
  }

  function resizeMap() {
  if(map) { map.invalidateSize(); }
  }

</script>
  
<svelte:head>
  <link rel="stylesheet" href="https://cdn.skypack.dev/leaflet@1.6.0/dist/leaflet.css"
  integrity="sha512-xwE/Az9zrjBIphAcBb3F6JVqxf46+CDLwfLMHloNu6KEQCAWi6HcDUbeOfBIptF7tcCzusKFjFw2yuvEpDL9wQ=="
  crossorigin=""/>
</svelte:head>

{#if button === true}
  <button on:click={useCurrentLocation}>
    <strong>
      Mein Standort
    </strong>
  </button>
{/if}

<div style="height:400px;width:100%" use:mapAction />

<svelte:window on:resize={resizeMap} />

<style>
  button {
    width: 20%;
    display: block;
    text-align: center;
    text-decoration: black;
    border: 3px solid black;
    border-radius: 0;
    margin-top: 1em;
    margin-left: auto;
    margin-right: auto;
    transition: width 0.4s ease-in-out;
    background: rgb(149,65,131,0.3);
    background: linear-gradient(90deg, rgba(149,65,131,0.3) 0%, rgba(191,166,227,0.3) 25%, rgba(125,185,241,0.3) 75%, rgba(68,109,137,0.3) 100%);
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  }
  button:hover{
    box-shadow: rgba(0, 0, 0, 0.16) 0px 4px 8px, rgba(0, 0, 0, 0.23) 0px 4px 8px;
  }
</style>